import good from "../../components/index/good.vue"
export default {
	name: "",
	components: {
		good,
	},
	props: {},
	data() {
		return {
			goodsList: [],
			active: 0,
			type:"",
			order:"asc",
			originalData:[]
		}
	},
	methods: {
		// 查看全部:默认综合   type:isNew新品 isHot人气推荐
		getGoodsType(type) {
			this.$api.getGoodsType(type).then(res => {
				if (res.status === 200) {
					this.goodsList = res.data.data
					this.originalData = res.data.data
					// let index=Math.floor((Math.random()*res.data.data.length))
					// this.img=res.data.data[index].primary_pic_url
				}
			}).catch(err => {
				console.log(err)
			})
		},
		// 全部升序or排列 order:asc升序 desc降序
		getGoodsSore(){
			this.active=1
			this.$api.getGoodsSore(this.type,this.order).then(res=>{
				if (res.status === 200) {
					this.goodsList = res.data.data
					if(this.order==="desc"){
						this.order='asc'
					}else{
						this.order="desc"
					}
				}
			}).catch(err=>{
				console.log(err)
			})
		},
		// 切换到综合或分类
		changeTitle(index){
			this.active=index
			this.goodsList=this.originalData
		}
		
	},
	mounted() {

	},
	onLoad(option) {
		this.getGoodsType(option.type)
		this.type=option.type
		let title = ""
		if (option.type === "isNew") {
			title = "新品首发"
		} else {
			title = "人气推荐"
		}
		uni.setNavigationBarTitle({
			title: title
		});
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
